.notification-page {
    max-width: 600px;
    margin: 20px auto;
    padding: 10px;
    background-color: #f8f9fa;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .page-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .notification-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  